    width: 130px;
    background-color: rgb(255, 222, 115);
    position: relative;
}

.cover {
    background-color: rgb(236,164,59);
}

.dripping {
    position: absolute;
    top: 100%;
    left: 0;
    width: 127px;
}

.container {
    display: flex;
    justify-content: center;
}

#content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    margin: 4rem 1rem;
    border-radius: 3rem;
    background-color: rgb(255, 222, 115);
    width: 50%;
    padding: 3rem; 
}

.heading-container {
    display: flex;
    justify-content: center;
    align-items: center;
