    display: flex;
    align-items: center;
    justify-content: center;
}

.short-header {
    width: 20rem;
}

.short-header > h1 {
    font-size: 2rem;
}

.first {
    margin-top: 0;
}

h1 {
    font-family: "potta_oneregular";
